.game {
  text-align: center;
  background: url(https://img.freepik.com/free-photo/texture-background_1404-99.jpg?w=2000);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 20%;
}
.error,
.score {
  color: maroon;
  padding: 3px;
  background-color: lightslategrey;
  border-radius: 2rem;
  font-weight: bold;
  margin: 1em;
}
.submit {
  margin: 2em;
  background: linear-gradient(to right, red, purple);
  color: white;
  height: 2em;
  width: 6em;
}

.submit:hover {
  color: black;
}
#congrats {
  color: darkgreen;
}
